import React from 'react';
import { Card, Row, Col } from 'react-bootstrap';
import { Chart } from 'react-google-charts';

const Cards = () => {
  const chartData = [
    ['Task', 'Hours per Day'],
    ['Work', 11],
    ['Eat', 2],
    ['Commute', 2],
    ['Watch TV', 5],
    ['Sleep', 8],
  ];

  return (
    <div style={{ color: 'black', marginBottom: '30px' }}>
      Hello Shahrukh,
      <Row className="justify-content-center mt-3">
        <Col sm={12} md={6} lg={3} className="mb-3">
          <Card style={{ height: '150px', background: 'linear-gradient(lightcyan,lightblue)' }}>
            <Card.Body>
              <Card.Title>Booking Order</Card.Title>
              <Card.Text>1200</Card.Text>
            </Card.Body>
          </Card>
        </Col>

        <Col sm={12} md={6} lg={3} className="mb-3">
          <Card style={{ height: '150px', background: 'linear-gradient(lightpink,lightseagreen)' }}>
            <Card.Body>
              <Card.Title>Weekly Orders</Card.Title>
              <Card.Text>500</Card.Text>
            </Card.Body>
          </Card>
        </Col>

        <Col sm={12} md={6} lg={3} className="mb-3">
          <Card style={{ height: '150px', background: 'linear-gradient(lightskyblue,lightgray)' }}>
            <Card.Body>
              <Card.Title>Stock Available</Card.Title>
              <Card.Text>50</Card.Text>
            </Card.Body>
          </Card>
        </Col>

        <Col sm={12} md={6} lg={3} className="mb-3">
          <Card style={{ height: '150px', background: 'linear-gradient(lightsalmon,lightcyan)' }}>
            <Card.Body>
              <Card.Title>Top Selling Products</Card.Title>
              <Card.Text>100</Card.Text>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row className="justify-content-center mt-3">
        <Col sm={12} lg={6} className="mb-3">
          <Card>
            <Card.Body>
              <Card.Title>Overview</Card.Title>
              <Chart
                width={'100%'}
                height={'200px'}
                chartType="ColumnChart"
                loader={<div>Loading Chart</div>}
                data={chartData}
                options={{
                  title: 'My Daily Activities',
                }}
                rootProps={{ 'data-testid': '1' }}
              />
            </Card.Body>
          </Card>
        </Col>

        <Col sm={12} lg={6} className="mb-3">
          <Card>
            <Card.Body>
              <Card.Title>Overview</Card.Title>
              <Chart
                width={'100%'}
                height={'200px'}
                chartType="PieChart"
                loader={<div>Loading Chart</div>}
                data={chartData}
                options={{
                  title: 'My Daily Activities',
                }}
                rootProps={{ 'data-testid': '1' }}
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Cards;
